import React, { Component } from 'react';

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faPlus } from '@fortawesome/free-solid-svg-icons';

import Button from '../Button';
import Item from './Item';
import Form from '../Form';

import './Collection.scss';

class Collection extends Component {
  constructor(props) {
    super(props);

    this.state = {
      selectedItem: null
    };
  }

  selectItem(item) {
    this.setState({selectedItem: item});
  }

  renderItemList(items) {
    return items.map((item, index) => {
      return (
        <div className="collection__item" key={index}>
          <Item item={item}>
            <Button onClick={() => this.selectItem(item)} className="text--small" variant="default" block>
              <FontAwesomeIcon icon={faPlus} /> <span className="color--gray">Add to NASA collection</span>
            </Button>
          </Item>
        </div>
      )
    });
  }

  render() {
    const { items } = this.props;
    const { selectedItem } = this.state;

    return (
      <div className="collection">
        {
          items
          ? this.renderItemList(items)
          : ''
        }
        {
          selectedItem
          ? <Form item={selectedItem}/>
          : ''
        }

      </div>
    );
  }
}

export default Collection;