import React, { PureComponent } from 'react';
import { Link } from 'react-router-dom';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faPlay } from '@fortawesome/free-solid-svg-icons';

import './Thumbnail.scss';

class Thumbnail extends PureComponent {
  render() {
    const { type, nasaId, imageUrl } = this.props;
    let backgroundStyle = !!imageUrl
      ? {backgroundImage: `url('${imageUrl}')`}
      : {backgroundColor: '#CCC'}

    if (!!imageUrl){
      if (type === 'video') {
        return (
          <Link to={`/video/${nasaId}`}>
            <div className="thumbnail" style={backgroundStyle}>
              <div className="thumbnail__play">
                <FontAwesomeIcon icon={faPlay} />
              </div>
            </div>
          </Link>
        )
      } else {
        return (
          <div className="thumbnail" style={backgroundStyle}></div>
        )
      }
    } else {
      return (
        <div className="thumbnail" style={backgroundStyle}></div>
      )
    }
  }
}

export default Thumbnail;