import React, { Component } from 'react';
import { BrowserRouter as Router, Route } from 'react-router-dom';

import { CollectionPage, SearchPage, EditPage, VideoPage } from './app/pages';

import './app/styles/style.scss';

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  render() {
    return (
      <Router>
        <div>
          <Route exact path="/" component={CollectionPage} />
          <Route path="/nasa-search" component={SearchPage} />
          <Route path="/video/:nasaId" component={VideoPage} />
            <Route path="/edit/:nasaId" component={EditPage} />
        </div>
      </Router>
    );
  }
}

export default App;
